import React from "react"
import { graphql } from "gatsby"
import moment from 'moment';

import Layout from '../components/layout'
import SEO from '../components/seo'
import Title from '../components/Title'
import PostMeta from '../components/postMeta'
import BlogPost from '../components/blogPost'

function betterDate(date) {
  return moment(date).format('YYYY-MM-DD');
}


export default function Template({
  data, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data // data.markdownRemark holds our post data
  const { frontmatter, html } = markdownRemark
  let path = frontmatter.path;
  if (!path) {
    const __date = new Date(frontmatter.date);
    const year = __date.getFullYear().toString();
    let month = (__date.getMonth() + 1).toString();
    if (month.length < 2) {
      month = '0' + month;
    }
    path = `/${year}/${month}/${frontmatter.permalink}`;
  }
  return (
    <Layout
      headerImage={
        frontmatter.headerImage && (
          <img
            src={frontmatter.headerImage}
            alt=""
            style={{
              margin: '0px',
            }}
          />
        )
      }
    >
      <SEO
        title={frontmatter.title}
        keywords={[
          `blog`,
          `technology`,
          `photography`,
        ]}
      />
      <div className="blog-post-container">
        <BlogPost>
          <Title to={path}>
            {frontmatter.title}
          </Title>
          <PostMeta>
            Posted at {betterDate(frontmatter.date)}
          </PostMeta>
          <div
            className="blog-post-content"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </BlogPost>
      </div>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($title: String!) {
    markdownRemark(frontmatter: { title: { eq: $title } }) {
      html
      frontmatter {
        date
        path
        title
        permalink
        headerImage
      }
    }
  }
`
