import React from 'react'
import { Link } from 'gatsby'

const Title = ({ to, children }) => (
  <Link
    to={to}
    style={{
      fontWeight: '500',
      fontSize: '24px',
      color: '#2e405b',
      borderBottom: 'solid #2e405b 2px',
      textDecoration: 'none',
    }}
  >
    {children}
  </Link>
)

export default Title
